<template>
  <billjlb  v-ref:billktk :billselect="billselect" :billinfo="billinfo"></billjlb>
</template>

<script>
  import  {HttpResetClass} from 'vue-client'
  export default {
    title: "销售记录表信息",
    props:['billselect','selectone'],
    data(){
      return{
        billinfo: {f_billnumber:null},
        number: null
      }
    },
    ready() {
    },
    methods: {
      // 更新 确认卡编号
      async updateNumber() {
        let http = new HttpResetClass()
        let billinfo = Object.assign({}, this.billselect, this.billinfo)
        delete billinfo.id
        // 票据类型
        billinfo.f_bill_type = this.selectone
        let data = {
          billinfo: billinfo
        }
        await http.load('POST', 'rs/logic/makebillrecord', {data: data}, {resolveMsg: null, rejectMsg: "开通卡保存记录失败"})
      }
    }
  }
</script>

<style scoped>

</style>



